<template>
  <div>
    <mobile-header
      title="공지사항"
      left-icon="mdi-chevron-left"
      class="mb-0"
      style="box-shadow: 0px 2px 14px rgba(0, 0, 0, 0.05)"
      @close="backRouting"
    />

    <div id="m_admin-main__page" class="pt-0">
      <section class="sidebar-items__wraps notice">
        <div class="title-box">
          <h4 class="mb-0">전체 목록</h4>
        </div>
        <ul class="preview-box ma-0 pa-0">
          <li v-for="(notice, id) in getNoticeList" :key="id" class="py-1">
            <div>
              <div
                class="px-2"
                style="display: flex; justify-content: space-between"
                @click="noticeDetail(notice.id)"
              >
                <span
                  style="font-weight: 500; font-size: 16px; color: #3f3531"
                  >{{ notice.noticeTitle }}</span
                >
                <v-icon>{{
                  noticeDetailFlag === notice.id
                    ? "mdi-chevron-up"
                    : "mdi-chevron-down"
                }}</v-icon>
              </div>
              <v-divider class="mt-2"></v-divider>
              <div
                v-if="noticeDetailFlag === notice.id"
                style="background: #f8f6f5; padding: 24px 38px"
              >
                <p
                  style="
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 170%;
                    color: #3f3531;
                  "
                  v-html="changeTxtToHtml(notice.noticeContent)"
                ></p>

                <template v-for="(file, index) in notice.noticeFile">
                  <img
                    v-if="file.fileType === 'PICTURE'"
                    :key="index"
                    :src="file.filePath"
                    style="max-width: 100%; height: auto"
                  />
                  <video
                    v-if="file.fileType === 'MOVIE'"
                    :key="index"
                    width="100%"
                    height="auto"
                    controls
                  >
                    <source
                      v-if="file.fileExtension == '.mp4'"
                      :src="file.filePath"
                      type="video/mp4;"
                    />
                    <source
                      v-if="file.fileExtension == '.ogv'"
                      :src="file.filePath"
                      type="video/ogg;"
                    />
                  </video>
                </template>
              </div>
            </div>
          </li>
        </ul>
      </section>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import dateFomat from "@/mixin/dateFomat";
import myMixin from "@/mixin/mixin";
import MobileHeader from "@/components/common/MobileHeader.vue";

export default {
  name: "ObituaryReg",
  mixins: [myMixin, dateFomat],
  components: {
    MobileHeader
  },
  props: {},
  data() {
    return {
      searchTxt: this.$route.query.searchTxt ? this.$route.query.searchTxt : "",
      notice: {
        id: null,
        noticeContent: this.$route.query.searchTxt
          ? this.$route.query.searchTxt
          : "",
        noticeTitle: this.$route.query.searchTxt
          ? this.$route.query.searchTxt
          : "",
        page: {
          page: this.$route.query.page ? this.$route.query.page : 0,
          size: this.$route.query.size ? this.$route.query.size : 5,
          sort: this.$route.query.sort ? this.$route.query.sort : "regDate,desc"
        }
      },
      noticeDetailFlag: false,
      pages: function() {
        const list = [];
        for (let i = this.startPage; i <= this.endPage; i += 1) list.push(i);
        return list;
      }
    };
  },
  computed: {
    ...mapGetters([
      "getNoticeList",
      "getNoticePage",
      "getNoticePageTotal",
      "getNoticeTotalElements"
    ]),
    startPage() {
      return parseInt((this.getNoticePage - 1) / 5) * 5 + 1;
    },
    endPage() {
      return (parseInt((this.getNoticePage - 1) / 5) + 1) * 5 <
        this.getNoticePageTotal
        ? (parseInt((this.getNoticePage - 1) / 5) + 1) * 5
        : this.getNoticePageTotal;
    }
  },
  mounted() {
    this.requestGetNoticeList();
  },
  beforeDestroy() {
    this.$store.commit("NOTICE_INIT");
  },
  methods: {
    noticeDetail(id) {
      if (this.noticeDetailFlag === id) {
        this.noticeDetailFlag = null;
        return;
      }
      this.noticeDetailFlag = id;
    },
    requestGetNoticeList() {
      const parameter = {
        notice: this.notice
      };
      this.$store.dispatch("REQUEST_NOTICE_LIST", parameter);
    },
    noticeMore() {
      const param = {
        page: this.notice.page.page + 1
      };
      this.requestNoticeList(param);
    },
    onPageChange(val) {
      const param = {
        page: val
      };
      this.requestNoticeList(param);
    },
    requestNoticeList(param) {
      let currentPage = 0;
      if (
        param !== undefined &&
        param !== null &&
        param.page !== undefined &&
        param.page !== null
      ) {
        currentPage = param.page - 1;
      }
      this.$router.push({
        path: "/notice",
        query: {
          page: currentPage,
          size: this.notice.page.size,
          sort: this.notice.page.sort
        }
      });
    },

    backRouting() {
      this.$router.push("/");
    }
  }
};
</script>
